
export default {
	GET_BOOKSTORE_ALLBOOKS (context) {
		/**
		*
		* 
		*cc
		*
		*/
		return context.$http({
			url : 'http://aishuwo.cn/api/bookServer.php',
			methods : 'GET',
			params : {
				flag : 'allbooks'
			},
			body : {

			},
			before: function () {
				context.$vux.loading.show({
					text: '...'
				});
			}
		})
	},
	GET_USED_ALLBOOKS (context) {
		/**
		*
		* 
		*cc
		*
		*/
		return context.$http({
			url : 'http://aishuwo.cn/api/bookServer.php',
			methods : 'GET',
			params : {
				flag : 'mallBooks'
			},
			body : {

			},
			before: function () {
				context.$vux.loading.show({
					text: '...'
				});
			}
		})
	},
	GET_BOOKINFO (context, book_id) {
		/**
		*
		* 
		* book_id
		* CC
		*
		*/
		return context.$http({
			url : 'http://aishuwo.cn/api/getInfo.php',
			methods : 'GET',
			params : {
				flag: 'book',
				book_id: book_id
			},
			body : {
				
			},
			before: function () {
				context.$vux.loading.show({
					text: '...'
				});
			}
		});
	},
	GET_USEDBOOKINFO (context, book_id) {
		/**
		*
		* 
		* book_id
		* CC
		*
		*/
		return context.$http({
			url : 'http://aishuwo.cn/api/bookServer.php',
			methods : 'GET',
			params : {
				flag: 'mallBook',
				bid: book_id
			},
			body : {
				
			},
			before: function () {
				context.$vux.loading.show({
					text: '...'
				});
			}
		});
	},
	SEARCH_BOOKSTORE (context, keyword) {
		/**
		*
		* 
		* 
		*keyword
		* CC
		*
		*/
		return context.$http({
			url : 'http://aishuwo.cn/api/bookServer.php',
			methods : 'GET',
			params : {
				flag: 'search',
				keyword: keyword
			},
			body : {
				
			}
		});
	}
}


